<template>
	<view style="padding-top: 22rpx;">
		<uni-list>
			<uni-list-item>
				<view slot="header" class="form-title">
					<text class="required-s">*</text>
					隐患类型
				</view>
				<view slot="footer">
					<picker :value="form.troubleType" @change="changeTroubleType" :range="troubleTypeOptions" range-key="label">
						<text class="cuIcon-right right-icon"></text>
						<input placeholder="请选择" name="input" :value="form.troubleTypeName" readonly style="float:right" />
					</picker>
					<view class="error-style" v-if="errMsg.troubleType != ''">{{ errMsg.troubleType }}</view>
				</view>
			</uni-list-item>
			<uni-list-item>
				<view slot="header" class="form-title">
					<text class="required-s">*</text>
					设备设施类别
				</view>
				<view slot="footer">
					<picker :value="form.equipmentType" @change="changeEquipmentType" :range="equipmentTypeOptions" range-key="label">
						<text class="cuIcon-right right-icon"></text>
						<input placeholder="请选择" name="input" :value="form.equipmentTypeName" readonly style="float:right" />
					</picker>
					<view class="error-style" v-if="errMsg.equipmentType != ''">{{ errMsg.equipmentType }}</view>
				</view>
			</uni-list-item>
			<uni-list-item>
				<view slot="header" class="form-title">
					<text class="required-s">*</text>
					详细内容
				</view>
				<view slot="footer">
					<textarea @input="inputContent" :value="form.content" placeholder="请输入" style="width:400rpx"></textarea>
					<view class="error-style" v-if="errMsg.content != ''">{{ errMsg.content }}</view>
				</view>
			</uni-list-item>
			<uni-list-item link @click="chooseLocation">
				<view slot="header" class="form-title">
					<text class="required-s">*</text>
					隐患地点
				</view>
				<view slot="footer" style="font-size: 28rpx;">
					{{ form.place }}
					<view class="error-style" v-if="errMsg.place != ''">{{ errMsg.place }}</view>
				</view>
			</uni-list-item>
			<uni-list-item link>
				<view slot="header" class="form-title">
					<text class="required-s">*</text>
					上传图片
				</view>
				<view slot="footer">
					<view style="width:480rpx;text-align: right;">
						<uni-badge text="x" absolute="rightTop" type="error" :offset="[8, 0]" @click="removeImage(item, index)" v-for="(item, index) in fileList" :key="index">
							<image class="image-style" :src="formatimageUrl(item.picUrl)"></image>
						</uni-badge>
						<view class="upload-image-box">
							<uni-icons type="plusempty" size="50" @click="chooseCameraImg" color="#efefef" style="vertical-align: top;"></uni-icons>
						</view>
					</view>
					<view class="error-style" v-if="errMsg.image != ''">{{ errMsg.image }}</view>
				</view>
			</uni-list-item>
		</uni-list>
		<view class="flex flex-direction" style="padding:30rpx"><button class="cu-btn bg-blue lg" @click="report">提 交</button></view>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			form: {
				content: '',
				troubleType: '',
				troubleTypeName: '',
				equipmentType: '',
				equipmentTypeName: '',
				place: '',
				shape: ''
			},
			errMsg: {
				content: '',
				troubleType: '',
				equipmentType: '',
				place: '',
				image: ''
			},
			troubleTypeOptions: [],
			equipmentTypeOptions: [],
			fileList: [],
			id: ''
		};
	},
	onLoad(options) {
		console.log(options)
		this.id = options.id;
		this.getDicData();
	},
	methods: {
		getDicData() {
			taskAPI.getSysDict({ type: 'hidden_danger_type' }).then(res => {
				if (res.code == 200) {
					this.troubleTypeOptions = res.data;
				}
			});
		},
		changeTroubleType(e) {
			let temp = this.troubleTypeOptions[e.detail.value];
			this.form.troubleType = temp.value;
			this.form.troubleTypeName = temp.label;
			this.errMsg.troubleType = '';
			//给设备设施类别的option赋值
			this.equipmentTypeOptions = temp.sysDictList;
			this.form.equipmentType = ''
			this.form.equipmentTypeName = '';
		},
		changeEquipmentType(e) {
			this.form.equipmentType = this.equipmentTypeOptions[e.detail.value].value;
			this.form.equipmentTypeName = this.equipmentTypeOptions[e.detail.value].label;
			this.errMsg.equipmentType = '';
		},
		chooseLocation() {
			//选择地点
			uni.chooseLocation({
				success: res => {
					this.form.place = res.name;
					this.form.shape = `POINT(${res.longitude} ${res.latitude})`;
					this.errMsg.place = '';
				}
			});
		},
		inputContent(event) {
			this.form.content = event.detail.value;
			this.errMsg.content = '';
		},
		formatimageUrl(url) {
			return process.uniEnv.FILE_URL + url;
		},
		removeImage(item, index) {
			//移除图片
			this.fileList.splice(index, 1);
		},
		checkForm() {
			let flag = true;
			if (this.form.troubleType == '') {
				this.errMsg.troubleType = '请选择隐患类型';
				flag = false;
			}
			if (this.form.equipmentType == '') {
				this.errMsg.equipmentType = '请选择设备设施类别';
				flag = false;
			}
			if (this.form.content == '') {
				this.errMsg.content = '请填写详细内容';
				flag = false;
			}
			if (this.form.place == '') {
				this.errMsg.place = '请选择隐患地点';
				flag = false;
			}
			if (this.fileList.length == 0) {
				this.errMsg.image = '请上传图片';
				flag = false;
			}
			return flag;
		},
		report() {
			let flag = this.checkForm();
			if (flag) {
				uni.showModal({
					content: '是否确认提交？',
					success: rs => {
						console.log(rs);
						if (rs.confirm) {
							//点击了确定按钮
							let param = {
								eventType:this.form.troubleType,
								devType: this.form.equipmentType,
								eventDetail: this.form.content,
								eventLocation: this.form.place,
								picUrlList: this.fileList,
								shape: this.form.shape,
							};
							if(this.id&&this.id!=='undefined'){
								param.taskId=this.id
								param.reportType=0
							}else{
								param.reportType=1
							}
							console.log(param)
							taskAPI.reportEvent(param).then(res => {
								if (res.code == 200) {
									uni.showToast({
										icon: 'none',
										title: '提交成功',
										success: () => {
											uni.navigateBack()
										}
									});
								}
							});
						}
					}
				});
			}
		},
		chooseCameraImg() {
			let _this = this;
			uni.chooseImage({
				count: 5, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: function(res) {
					for (let i = 0; i < res.tempFilePaths.length; i++) {
						uni.uploadFile({
							url: process.uniEnv.BASE_URL + '/blueocean/file/uploadImage',
							name: 'uploadfile',
							filePath: res.tempFilePaths[i],
							header: {
								Authorization: uni.getStorageSync('token')
							},
							success: function(rs) {
								var data = JSON.parse(rs.data);
								if (data.code == 200) {
									_this.$notify({
										type: 'success',
										message: '上传成功'
									});
									_this.fileList.push({
										picUrl: data.data
									});
									_this.errMsg.image = '';
								}
							},
							fail: function(rs) {
								_this.$notify({
									type: 'danger',
									message: rs.data || '上传失败'
								});
							}
						});
					}
				}
			});
		}
	}
};
</script>

<style scoped>
input,
textarea {
	text-align: right;
}
</style>
